function spotFixSplitUrl(url) {
    try {
        if (!url || url.trim() === '') {
            return '';
        }
        const u = new URL(url);
        const domain = u.host;

        const segments = u.pathname.split('/').filter(Boolean);

        if (segments.length === 0) {
            return domain;
        }

        const reversed = segments.reverse();
        reversed.push(domain);
        return reversed.join(' / ');
    } catch (error) {
        return '';
    }
}

